import * as React from 'react';
import * as ReactTooltip from 'react-tooltip';
import ItemService from './Services/ItemsService';

interface IItemProps {
  hash: string
}

class Item extends React.Component<IItemProps, any> {
  public render() {
    return (
      <React.Fragment>
        <ReactTooltip 
          id={`tooltip-${this.props.hash}`}
          effect="solid"
          delayShow={100}
        >
        <div className="item-tooltip legendary">
          <div className="item-tooltip-header">
            <img src={ItemService.imageURL(this.props.hash)} />
            <div className="info">
              <span>{ItemService.name(this.props.hash)}</span>
              <span>{ItemService.type(this.props.hash)}</span>
            </div>
          </div>
          <div className="item-tooltip-body">
            <img src={ItemService.backgroundImageURL(this.props.hash)} />
          </div>
        </div>
        </ReactTooltip>
        <img
          data-tip={true}
          data-for={`tooltip-${this.props.hash}`}
          className="item-image"
          style={{ opacity: ItemService.has(this.props.hash) ? 1 : 0.25 }}
          src={ItemService.imageURL(this.props.hash)}
        />

      </React.Fragment>
    );
  }
}

export default Item;



